 святих Йоакима і Анни, що відноситься до стилю бароко та середини XVIII сторіччя. Неподалік знаходиться ще одна красива будівля - кафедральний Собор Різдва Христового, що відноситься до Православної Церкви України. ")]),_c('div',[_c('img',{staticClass:"city-img",attrs:{"src":require("../assets/about-city/02.jpg"),"alt":"catolic"}}),_c('img',{staticClass:"city-img",attrs:{"src":require("../assets/about-city/03.jpg"),"alt":"orthodox"}})])]),_c('div',{staticClass:"text"},[_c('p',[_vm._v("В самому центрі міста розкинувся затишний Слов'янський парк. Тут можна погуляти з дітьми, влаштувати пікнік з коханою людиною, або просто посидіти в тіні дерев із книжкою. В парку розташовано кілька скульптур давньоруських князів, письменників, є два джерела з прозорою чистою водою, мотузковий парк та невеличкий спортивний майданчик.")]),_c('img',{staticClass:"city-img",attrs:{"src":require("../assets/about-city/05.jpg"),"alt":"park"}})]),_c('div',{staticClass:"text"},[_c('p',[_vm._v("Володимир має розвинену транспортну систему. Громадським транспортом можна дістатися до центру з будь-якої частини міста. Дивує якість доріг, до якої не дотягнуть деякі великі міста нашої країни. У Володимирі присутні популярні мережеві магазини і сервіси - Eva, Watsons, Аврора, АТБ, Rozetka, Нова Пошта, Delivery, SPAR, Сім-23,Dnipro-M, Алло, Фокстрот. В самому центрі розташовані продуктовий та речовий ринки та магазини будівельних матеріалів. Присутні готель, автостанція та залізничний вокзал.")]),_c('div',{staticClass:"brand-logos"},[_c('div',{staticClass:"row"},[_c('img',{staticClass:"round-brand-image col",attrs:{"src":require("../assets/trade/7-23_logo.png"),"alt":"7-23"}}),_c('img',{staticClass:"round-brand-image col",attrs:{"src":require("../assets/trade/spar_logo.png"),"alt":"spar"}}),_c('img',{staticClass:"round-brand-image col",attrs:{"src":require("../assets/trade/rozetka_logo.png"),"alt":"rozetka"}}),_c('img',{staticClass:"round-brand-image col",attrs:{"src":require("../assets/trade/novaposht